import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';

class Home extends Component {
  render() {
    return (
      <div className="page">
        <div className="container">
          <NavLink
            to='/VideoRecorder'
          ></NavLink>
        </div>
      </div>
    )
  }
}

export default Home;



