import React, { Component } from 'react';

class VideoRecorder extends Component {
  constructor() {
    super();
    this.state = {
      constraints: { "video": true, "audio" : true },
      stream: null,
      vidSrc: null,
      recorder: null,
      recordedChunks: [],
      fadein: "",
      fadeout: "",
      timeRemaining: 7,
    };
    this.startFunction = this.startFunction.bind(this);
    this.stopFunction = this.stopFunction.bind(this);
  }

  componentDidMount() {
    navigator.mediaDevices.getUserMedia(this.state.constraints)
      .then((stream) => {
        let recorder = new MediaRecorder(stream,{mimeType: "video/webm"});
        recorder.ondataavailable = (e) => {this.state.recordedChunks.push(e.data)};
        this.setState({
          vidSrc: window.URL.createObjectURL(stream),
          stream: stream,
          recorder: recorder,
        },() => this.startFunction())
      })
      .catch((e) => {console.error(e)})
    setTimeout(() => {
      this.setState({fadeout: " fade-out"});
    },1000);
    setTimeout(() => {
      this.setState({fadein: ""});
    },this.state.timeRemaining*1000 - 1500);
  }

  startFunction() {
    this.setState({fadein: " fade-in"});
    this.state.recorder.start(100);
    let interval = setInterval(() => {
      this.setState({
        timeRemaining: this.state.timeRemaining - 1
      }, () => {
        if (this.state.timeRemaining === 0) {
          this.stopFunction();
          clearInterval(interval)
        }
      });
    },1000);
  }

  stopFunction() {
    this.state.recorder.stop();
    this.state.stream.getTracks().map(track => { track.stop(); });

    let blob = new Blob(this.state.recordedChunks, {type: "video/webm"});
    let url = URL.createObjectURL(blob);
    var a = document.createElement("a");
    document.body.appendChild(a);
    a.style = "display: none";
    a.href = url;
    a.download = '' + new Date() + '.webm';
    // TODO uncomment to allow download
    a.click();
    setTimeout(function() { URL.revokeObjectURL(url); }, 100);
    this.props.history.push('/');
  }

  render() {
    return (
      <div className="page">
        <div className="videoContainer">
          <video
            className={"recorder"+this.state.fadein}
            src={this.state.vidSrc}
            autoPlay="true" />
          <video
            className={"countdown"+this.state.fadeout}
            id="countdown"
            src={require('./countdown-short.mp4')}
            autoPlay="true"
            muted />
          <div className="ticker">
            Time remaining: {this.state.timeRemaining+1} {this.state.timeRemaining+1 > 1 ? 'seconds' : 'second'}
          </div>
        </div>
      </div>
    );
  }
}

export default VideoRecorder;



